<template>
  <div class="share-get-users">
    <div class="base-wrapper">
      <img src="./images/invite@2x.png" alt="">
      <img src="./images/outer1@2x.png" alt="">
      <div class="part2">
        <img src="./images/outer2@2x.png" alt="">
        <div class="investor-btn" @click="btnClick"></div>
      </div>
      <div class="part3">
        <img src="./images/outer3@2x.png" alt="">
        <div class="project-btn project-btn1" @click="btnClick"></div>
        <div class="project-btn project-btn2" @click="btnClick"></div>
        <div class="project-btn project-btn3" @click="btnClick"></div>
        <div class="project-btn project-btn4" @click="btnClick"></div>
        <div class="project-btn project-btn5" @click="btnClick"></div>
        <div class="project-btn project-btn6" @click="btnClick"></div>
        <div class="project-btn project-btn7" @click="btnClick"></div>
        <div class="project-btn project-btn8" @click="btnClick"></div>
        <div class="project-btn project-btn9" @click="btnClick"></div>
        <div class="project-btn project-btn10" @click="btnClick"></div>
      </div>
      <img src="./images/outer4@2x.png" alt="">
      <img src="./images/outer5@2x.png" alt="">
      <!-- <img src="./images/outer6@2x.png" alt=""> -->
    </div>
    <div class="footer-fixed">
      <div class="footer-wrapper">
        <div class="footer-btn" @click="btnClick"></div>
      </div>
    </div>
    <!-- login -->
    <div class="login-wrapper" v-if="showLogin">
      <div class="login-inner">
        <div class="close-btn" @click="closeLogin"></div>
        <div class="title">亲，请先注册登录呦~</div>
        <label class="input-item">
          <div class="input-title">手机号</div>
          <input class="phone-input" v-model="phone" placeholder="请输入手机号" type="tel" maxlength="11" @input="telInput()" >
        </label>
        <label class="input-item">
          <div class="input-title">验证码</div>
          <input class="code-input" v-model="code" placeholder="请输入验证码" type="tel" maxlength="6">
          <div class="code-btn" :class="{'enabled': codeEnabled}" @click="getCode"></div>
          <div class="note"></div>
        </label>
        <div class="login-btn" @click="goLogin">登录</div>
      </div>
    </div>
    <pem-toast ref="toastEle" :text="toastTxt"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi } from 'api'
import { setTitle, validatePhone } from 'common/js/util'
import storage from 'common/js/storage.js'

export default {
  data () {
    return {
      showLogin: false,
      toastTxt: '',
      inviterzj: '',
      source: '',
      phone: null,
      code: null,
      codeEnabled: false,
      lxphone: '',
      time: 60,
      timer: null
    }
  },
  components: {
    PemScrollView,
    PemToast
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('邀请好友')
      this.wxShare({
        title: '找项目、找投资来【项目工场APP】，帮您快速信息对接~',
        content: '投融圈都在用的【投融资信息对接服务平台】'
      })
      this.inviterzj = this.$route.query.inviterzj || ''
      this.source = this.$route.query.source || ''
      this.lxphone = storage.get('lxphone') || ''
      // Sharing statistics
      if (this.$route.query.fapp) {
        MiniSiteApi.inviteFriendLog(this.$route.query.fapp)
      }
    },
    btnClick () {
      if (this.lxphone) {
        window.location.href = window.location.origin + '/download-lead'
      } else {
        this.showLogin = true
      }
    },
    closeLogin () {
      this.showLogin = false
    },
    telInput () {
      this.phone = this.phone.trim()
      this.codeEnabled = !!this.phone
    },
    getCode () {
      if (!this.phone || !this.codeEnabled) return
      if (!validatePhone(this.phone)) {
        this.showToast('请输入正确的手机号')
        return
      }
      this.codeEnabled = false
      this.timer = setInterval(() => {
        if ((this.time--) <= 0) {
          this.time = 60
          this.codeEnabled = true
          clearInterval(this.timer)
        }
      }, 1000)
      this._checkMobile(this.phone, (data) => {
        if (data.verifyCode) {
          this.code = data.verifyCode.replace(',repeat', '')
        }
        if (+data.resultcode === 1) {
          this.codeEnabled = false
        } else {
          let notemsg = {
            '-11': '手机号为空',
            '-12': '手机号输入不正确',
            '-13': '获取验证码次数太多',
            '-14': '获取验证码间隔太短'
          }[data.resultcode] || '未知错误，请联系管理员'
          this.showToast(notemsg)
          this.codeEnabled = true
        }
      })
    },
    goLogin () {
      let regCode = /^\d{6}$/
      if (!validatePhone(this.phone)) {
        this.showToast('请输入正确的手机号')
        return
      }
      if (!regCode.test(this.code)) {
        this.showToast('请输入正确的验证码')
        return
      }
      let options = {
        inviterzj: this.inviterzj,
        source: this.source,
        mobile: this.phone,
        code: this.code
      }
      this._login_lx(options, (data) => {
        if (+data.resultcode === 1) {
          this.lxphone = this.phone
          storage.set('lxphone', this.phone)
          this.showLogin = false
          window.location.href = window.location.origin + '/download-lead'
        } else {
          let notemsg = {
            '-11': '手机号为空',
            '-12': '验证码为空',
            '-13': '验证码失效',
            '-14': '验证码错误'
          }[data.resultcode] || '未知错误，请联系管理员'
          this.showToast(notemsg)
        }
      })
    },
    showToast (txt) {
      if (!txt) return
      this.toastTxt = txt
      this.$refs.toastEle.show()
    },
    hideToast () {
      this.$refs.toastEle.hide()
    },
    _checkMobile (mobile, callback) {
      MiniSiteApi.checkMobel(mobile).then(data => {
        callback(data)
      })
    },
    _login_lx (options, callback) {
      MiniSiteApi.login_lx(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-moz-placeholder
    color: #A7A7A7;
  ::-webkit-input-placeholder
    color: #A7A7A7;
  ::-ms-input-placeholder
    color: #A7A7A7;
  .share-get-users
    font-size:0;
    background-image: linear-gradient(0deg, #F77063 0%, #EE5A4C 100%);
    max-width: 750px;
    margin: 0 auto;
    padding-bottom: 1.3rem
    .base-wrapper 
      img
        width:100%;
      .part2
        position: relative;
        height: 8.82rem;
        .investor-btn 
          position: absolute;
          left: 2.78rem;
          bottom: 0.36rem;
          width: 1.96rem;
          height: 0.42rem;
          bg-image('./images/bt_duijie')
          background-size: 100% 100%;
      .part3
        position: relative;
        height: 16.88rem;
        .project-btn 
          position: absolute;
          right: 0.60rem;
          width: 1.24rem;
          height: 0.42rem;
          bg-image('./images/bt_see')
          background-size: 100% 100%;
        .project-btn1
          top: 1.98rem;
        .project-btn2
          top: 3.54rem;
        .project-btn3
          top: 5.10rem;
        .project-btn4
          top: 6.66rem;
        .project-btn5
          top: 8.22rem;
        .project-btn6
          top: 9.78rem;
        .project-btn7
          top: 11.34rem;
        .project-btn8
          top: 12.90rem;
        .project-btn9
          top: 14.46rem;
        .project-btn10
          top: 16.02rem;
    .footer-fixed
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 5;
      .footer-wrapper
        position: relative;
        width: 7.50rem;
        height: 1.34rem;
        background: #F66E61;
        margin: 0 auto;
      .footer-btn
        position: absolute;
        left: 0.60rem;
        top: 0.20rem;
        width: 6.30rem;
        height: 0.96rem;
        bg-image('./images/bt_kuaisu')
        background-size: 100% 100%
    .login-wrapper
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 10
      background: rgba(0,0,0,0.50)
      .login-inner
        position: absolute
        top: 3.06rem
        left: 50%
        transform: translate(-50%, 0)
        width: 295px
        height: 233px
        background: #ffffff
        border-radius: 8px
        overflow: hidden
        .close-btn
          position: absolute
          top: 0
          right: 0
          width: 42px
          height: 42px
          bg-image('./images/close')
          background-size: 12px 12px
          background-repeat: no-repeat
          background-position: center
        .title
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #202020;
          text-align: center
          padding: 32px 0 25px
        .input-item
          position: relative
          display: block
          min-height: 26px 
          padding-bottom: 20px
          .input-title
            position absolute
            left: 24px
            top: 0
            font-size: 13px;
            color: #333333;
            line-height: 26px
            padding-right: 8px
            bg-image('./images/input-require')
            background-size: 7px 9px
            background-repeat: no-repeat
            background-position: right center
          input
            box-sizing: border-box
            position absolute
            left: 80px
            background: #F5F5F5;
            border-radius: 2px;
            width: 191px;
            height: 26px;
            line-height: 26px;
            font-size: 11px;
            padding: 5px 8px
            &.code-input
              width: 115px
          .code-btn
            position absolute
            right: 24px
            top: 0
            width: 70px
            height: 25px
            bg-image('./images/input-code-disabled')
            background-size: 100% 100%
            &.enabled
              bg-image('./images/input-code-enabled')
          .note
            position absolute
            top: 31px
            left: 89px
            width: 72px
            height: 13px
            bg-image('./images/note')
            background-size: 100% 100%
        .login-btn
          position: absolute
          bottom : 0
          left: 0
          width: 100%
          height: 45px
          line-height: 45px
          border-top: 1px solid #DBDBDB
          font-family: PingFangSC-Regular;
          font-size: 15px;
          color: #FFD100;
          letter-spacing: -0.05px;
          text-align: center
</style>



